import { Component, OnInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MessageService } from '../message.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'cd',
  templateUrl: './corona-data.component.html',
  styleUrls: ['./corona-data.component.css']
})
export class CoronaDataComponent implements OnInit {
  Corona: any = [];
  title: string;
  cases: string;
  recovered: string;
  deaths: string;

  messages: any[] = [];
  subscription: Subscription;
  country:string;

  constructor(private api: HttpClient, public messageService: MessageService) {
    this.subscription = this.messageService.getMessage().subscribe(message => {
       
      if(message) {
        this.messages.push(message);
        this.country = this.messages.toString();
        if(this.country=='all'){
          this.api.get('https://coronavirus-19-api.herokuapp.com/all')
          .subscribe(
            x => { this.Corona = x; }
          )
          this.messages=[];
        }
        else{
        this.api.get('https://coronavirus-19-api.herokuapp.com/countries/'+this.country+'')
          .subscribe(
            x => { this.Corona = x; }
          )
          this.messages=[];
        }
         
      }
     
      
    });
   
  }
  ngOnInit() {
    this.api.get('https://coronavirus-19-api.herokuapp.com/all')
    .subscribe(
      x => { this.Corona = x; }
    )
    this.messages=[];
  
  }
  
}


