import { Component, OnInit, Input } from '@angular/core'; 
import { MessageService } from '../message.service';

@Component({
  selector: 'cp111',
  templateUrl: './corona-component.component.html',
  styleUrls: ['./corona-component.component.css']
})
export class CoronaComponentComponent implements OnInit {
  country: string;
  constructor(private messageService: MessageService) {
    this.country='all';
   }

  sendMessage(): void {
    this.messageService.sendMessage(this.country);
    this.messageService.clearMessages();
  }

  clearMessages(): void {
    this.messageService.clearMessages();
  }
  ngOnInit() {
  }

}
